/* About css */

.about {
  background-color: white;
  padding-top: 5rem;
  padding-bottom: 2rem;
  margin-left: 2rem;
  margin-right: 2rem;
}
.sub-heading {
  font-size: 2rem;
  font-weight: bold;
  color: var(--black);
  margin-left: 56rem;
}
.heading {
  font-size: 2rem;
  font-weight: bold;
  color: var(--black);
  margin-left: 50rem;
}
.about .row {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: center;
}

.about .row .image {
  flex: 1 1 45rem;
}

.about .row .image img {
  width: 100%;
}

.about .row .content {
  flex: 1 1 45rem;
}

.about .row .content h3 {
  color: var(--black);
  font-size: 2rem;
  padding-top: 3rem;
  font-weight: bold;
  margin-left: 52rem;
}

.about .row .content p {
  color: var(--light-color);
  font-size: 1.5rem;
  padding: 0.5rem 0;
  line-height: 2;
  margin-left: 3rem;
  margin-right: 3rem;
}

.about .row .content .icons-container {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  padding: 1rem 0;
  margin-top: 0.5rem;
}

.about .row .content .icons-container .icons {
  background: black;
  border-radius: 0.5rem;
  border: 0.1rem solid rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  flex: 1 1 17rem;
  padding: 1.5rem 1rem;
  color: white;
}

.about .row .content .icons-container .icons i {
  font-size: 2.5rem;
  color: var(--green);
}

.about .row .content .icons-container .icons span {
  font-size: 1.5rem;
  color: var(--black);
}
